#title {
  text-align: center;
  font-size: 54px;
  font-weight: bold;
  background-color: #F6F6F6;
}

#sudoku-container {
  width: 520px;
  height: 520px;
  -ms-flex-item-align: center;
      align-self: center;
  -ms-flex-pack: center;
      justify-content: center;
  background-color: lightblue;
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: justify;
      justify-content: space-between;
  padding-top: 10px;
  padding-bottom: 15px;
  /*padding-right: 10px;*/
  padding-left: 10px;
}

.sudoku-row-of-boxes {
  margin-top: 10px;
}

#below-sudoku {
  position: absolute;
  top: 90%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  margin-top: 20px;
  display: inline-block;
}

#clear {
  margin-left: 20px;
}

#solve {
  background-color: green;
}



